<template>
    <el-row :gutter="20">
        <el-col :span="8" style="margin-bottom:10px;height:210px">
            <el-card class="box-card" style="height:100%">
                <div slot="header" class="clearfix">
                    <span></span>
                </div>
                <div class="text item">
                    <div style="margin-bottom: 10px;">
                        <el-button-group>
                            <el-button @click="confirmGen('traffic', '')"></el-button>
                            <el-button @click="confirmGen('customer', '')"></el-button>
                            <el-button @click="confirmGen('global', '')"></el-button>
                        </el-button-group>
                    </div>
                    <!-- <div class="well"></div> -->
                    <el-tag type="danger">
                        <i class="el-icon-warning" style="padding-right:10px"></i>
                    </el-tag>
                </div>
            </el-card>
        </el-col>

        <el-col :span="8" style="margin-bottom:10px;height:210px">
            <el-card class="box-card" style="height:100%">
                <div slot="header" class="clearfix">
                    <span>
                        
                        <router-link :to="{path : '/snapshot/list'}"></router-link>
                    </span>
                </div>
                <div class="text item">
                    <div style="margin-bottom: 10px;">
                        <el-button-group>
                            <el-button @click="confirmDel('traffic', '')"></el-button>
                            <el-button @click="confirmDel('customer', '')"></el-button>
                            <el-button @click="confirmDel('global', '')"></el-button>
                        </el-button-group>
                    </div>
                    <!-- <div class="well"></div> -->
                    <el-tag type="danger">
                        <i class="el-icon-warning" style="padding-right:10px"></i>
                    </el-tag>
                </div>
            </el-card>
        </el-col>
    </el-row>
</template>

<script>
export default {
    methods: {
        confirmDel(type, text) {
            this.$confirm("" + text, "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning"
            })
                .then(() => {
                    this.delSnapshot(type);
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
        },
        async delSnapshot(type) {
            let res = await this.$api.user.deleteLastSnapshotByGo(type);
            if (res.errcode == 0) {
                // this.$bootbox.alert("");
                this.$message({
                    showClose: true,
                    message: "",
                    type: "success"
                });
            } else {
                // this.$bootbox.alert(res.errmsg);
                this.$message({
                    showClose: true,
                    message: res.errmsg,
                    type: "error"
                });
            }
        },
        confirmGen: function(type, text) {
            this.$confirm("" + text, "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning"
            })
                .then(() => {
                    this.genSnapshot(type);
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
        },
        async genSnapshot(type) {
            let res = await this.$api.user.genSnapshotByGo(type);
            if (res.errcode == 0) {
                this.$message({
                    showClose: true,
                    message: "",
                    type: "success"
                });
            } else {
                // this.$bootbox.alert(res.errmsg);
                this.$message({
                    showClose: true,
                    message: res.msg,
                    type: "error"
                });
            }
        }
    }
};
</script>

