<template>
    <el-row>
        <el-row>
            <el-button
                type="primary"
                @click="toEditExp"
                v-show="operateBtn.edit"
                :disabled="operating"
            ></el-button>
            <el-button
                type="success"
                @click="toUpOnlineExp(false)"
                v-show="operateBtn.audit_pass"
                :disabled="operating"
            ></el-button>
            <el-button
                type="primary"
                @click="toEditExp"
                v-show="operateBtn.volume"
                :disabled="operating"
            ></el-button>
            <el-button
                type="warning"
                @click="toRefuseExp(false)"
                v-show="operateBtn.audit_refuse"
                :disabled="operating"
            ></el-button>
            <el-button
                type="danger"
                v-show="operateBtn.do_full"
                @click="toDoFull"
                :disabled="operating"
            ></el-button>
            <el-button
                type="info"
                @click="toCancelFull"
                v-show="operateBtn.cancel_full"
                :disabled="operating"
            ></el-button>
            <el-button
                type="danger"
                @click="toDelExp(false)"
                v-show="operateBtn.offline"
                :disabled="operating"
            ></el-button>
            <el-button
                type="danger"
                @click="clickTo('toPushAll')"
                v-show="operateBtn.apply_full_volume"
                :disabled="operating"
            ></el-button>
        </el-row>
        <el-row style="margin-top:10px" v-if="operateBtn.show_volume_full_tip">
            <el-col :span="14" :offset="5">
                <el-alert
                    type="warning"
                    :closable="false"
                    center
                    show-icon
                    effect="dark"
                ></el-alert>
            </el-col>
        </el-row>
    </el-row>
</template>
<script>
export default {
    props: ["layerId", "layerInfo"],
    data() {
        return {
            operateBtn: {
                edit: false,
                audit_pass: false,
                volume: false,
                audit_refuse: false,
                offline: false,
                clone: false,
                re_up_online: false,
                apply_full_volume: false,
                do_full: false,
                cancel_full: false,
                add_user: false,
                show_volume_full_tip : false,
            },
            operating: false
        };
    },
    watch: {
        layerId: function() {
            this.getBtns();
        }
    },
    mounted() {
        this.getBtns();
    },
    methods: {
        async getBtns() {
            let res = await this.$api.exp.getExpBtnByGo(this.layerId);
            this.operateBtn = res.data;
        },
        toEditExp: function() {
            if (this.layerInfo.exp_layer_type == 1) {
                this.$router.push({
                    path: "/lab/custom_exp_edit",
                    query: { exp_layer_id: this.layerId }
                });
            } else {
                this.$router.push({
                    path: "/lab/edit",
                    query: { exp_layer_id: this.layerId }
                });
            }
        },
        async toRefuseExp(confirmed) {
            if (confirmed === false) {
                this.$confirm("?", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                    .then(() => {
                        this.toRefuseExp(true);
                    })
                    .catch(() => {});
                return false;
            }
            this.operating = true;
            let res = await this.$api.exp.auditRefuseByGo(this.layerId);
            if (res.errcode == 0) {
                this.$message({
                    message: "",
                    duration: 500,
                    onClose: () => {
                        this.$tool.reload();
                    }
                });
            } else {
                this.$message(res.errmsg || "");
            }
        },
        async toDelExp(confirmed) {
            if (confirmed === false) {
                this.$confirm("?", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                    .then(() => {
                        this.toDelExp(true);
                    })
                    .catch(() => {});
                return;
            }
            this.operating = true;
            let res = await this.$api.exp.auditOfflineByGo(this.layerId);
            if (res.errcode == 0) {
                this.$message({
                    message: "",
                    duration: 500,
                    type: "success",
                    onClose: () => {
                        this.$tool.reload();
                    }
                });
            } else {
                this.$message(res.errmsg || "");
            }
        },
        async toUpOnlineExp(confirmed) {
            if (confirmed === false) {
                this.$confirm("?", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                    .then(() => {
                        this.toUpOnlineExp(true);
                    })
                    .catch(() => {});
                return;
            }

            this.operating = true;
            let res = await this.$api.exp.auditOnlineByGo(this.layerId);
            if (res.errcode == 0) {
                this.$message({
                    message: "",
                    duration: 500,
                    type: "success",
                    onClose: () => {
                        this.$tool.reload();
                    }
                });
            } else {
                this.$message(res.errmsg || "");
            }
        },
        toDoFull() {
            this.$confirm("?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning"
            })
                .then(() => {
                    this.doFull();
                })
                .catch(() => {});
        },
        async doFull() {
            this.operating = true;
            let res = await this.$api.exp.doPushAllByGo(this.layerId);

            let successMsg = res.errmsg
            if(res.errcode == 0) {
                successMsg = ""
            }
            this.$message({
                message: successMsg,
                type: "success",
                duration: 500,
                onClose: () => {
                    this.$tool.reload();
                }
            });
        },
        async toCancelFull() {
            this.$confirm(
                "",
                "",
                {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                }
            )
                .then(() => {
                    this.cancelFull();
                })
                .catch(() => {});
        },
        async cancelFull() {
            this.operating = true;
            let res = await this.$api.exp.cancelPushAllByGo(this.layerId);
            let successMsg = res.errmsg
            if(res.errcode == 0) {
                successMsg = ""
            }
            this.$message({
                message: successMsg,
                type: "success",
                duration: 500,
                onClose: () => {
                    this.$tool.reload();
                }
            });
            
        },
        clickTo(action) {
            this.$emit(action);
        }
    }
};
</script>