<template>
    <el-col>
        <el-form :label-position="'top'" label-width="150px" v-model="layerInfo">
            <el-form-item label="">
                <el-input v-model="layerInfo.exp_layer_name" placeholder="20" @change="commitLayerInfo"></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-input
                    type="textarea"
                    v-model="layerInfo.exp_layer_comment"
                    rows="6"
                    placeholder=""
                    @change="commitLayerInfo"
                ></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-radio v-model="layerInfo.alloc_type_code" :label="4" @change="commitLayerInfo"></el-radio>
                <el-radio v-model="layerInfo.alloc_type_code" :label="5" @change="commitLayerInfo"></el-radio>
                <el-radio v-model="layerInfo.alloc_type_code" :label="6" @change="commitLayerInfo"></el-radio>
                <el-radio v-model="layerInfo.alloc_type_code" :label="7" @change="commitLayerInfo"></el-radio>
            </el-form-item>
        </el-form>
    </el-col>
</template>

<script>
export default {
    data() {
        return {
            layerInfo: {
                exp_layer_type : 1,
                alloc_type_code: 7,
                exp_layer_comment:
                    "\n\n\n\n",
                exp_layer_name: ""
            }
        };
    },
    mounted() {
        if (this.$store.state.create_exp.strategy_set_id < 1) {
            this.$emit("preStep");
            return;
        }
        if (
            Object.keys(this.$store.state.create_exp.exp_base_info).length > 0
        ) {
            console.log(this.$store.state.create_exp.exp_base_info);
            this.layerInfo = this.$store.state.create_exp.exp_base_info;
        }
    },
    methods: {
        commitLayerInfo() {
            this.$store.commit("setExpBaseInfo", this.layerInfo);
            return;
        },
        checkInputData() {
            if (this.layerInfo.exp_layer_name == "") {
                return [""];
            }

            if (this.layerInfo.exp_layer_name.length > 60) {
                return [" 1  60"];
            }

            if (this.layerInfo.exp_layer_comment == "") {
                return [""];
            }

            return [];
        }
    }
};
</script>
