<template>
    <el-row :gutter="20">
        <el-col :span="12" style="height:400px">
            <el-card class="box-card" style="overflow:visible;height:100%">
                <div slot="header" class="clearfix">
                    <span></span>
                </div>
                <el-row v-if="banTime.start == '' || banTime.end == ''">
                    <el-date-picker
                        style="padding:3px;width:100%"
                        format="yyyy-MM-dd HH:mm:ss"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        v-model="time"
                        type="datetimerange"
                        :picker-options="pickerOptions"
                        range-separator=""
                        start-placeholder=""
                        end-placeholder=""
                        align="right"
                    ></el-date-picker>

                    <div style="margin-top: 10px;margin-bottom: 10px;">
                        <el-button type="danger" @click="banOperate"></el-button>
                    </div>
                </el-row>

                <el-row v-if="banTime.start != '' && banTime.end != ''">
                    <el-form ref="form" label-width="180px">
                        <el-form-item label="">
                            <el-tag>{{banTime.start}}</el-tag>~
                            <el-tag>{{banTime.end}}</el-tag>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="openOperate"></el-button>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input type="textarea" v-model="banWhiteUser"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="saveBanWhiteUsers"></el-button>
                        </el-form-item>
                    </el-form>
                </el-row>
            </el-card>
        </el-col>

        <el-col :span="8" style="margin-bottom:40px;height:210px">
            <el-card class="box-card" style="overflow:visible;height:100%">
                <div slot="header" class="clearfix">
                    <span></span>
                </div>
                <div class="text item">
                    <div style="margin-bottom: 10px;">
                        <div class="form-inline">
                            <div class="form-group">
                                <el-select
                                    v-model="user"
                                    collapse-tags
                                    filterable
                                    placeholder=""
                                >
                                    <el-option
                                        v-for="item in user_list"
                                        :key="item.user_name"
                                        :value="item.user_name"
                                    ></el-option>
                                </el-select>
                            </div>&nbsp;&nbsp;&nbsp;
                            <el-button plain type="warning" @click="switchID"></el-button>
                        </div>
                    </div>
                </div>
            </el-card>
        </el-col>
    </el-row>
</template>

    <script>
import lodash from "lodash";
import PageViewList from "./PageViewList";
export default {
    data() {
        return {
            time: [],
            banTime: {
                start: "",
                end: "",
            },
            banWhiteUser : "",
            user: "none",
            user_list: [],
            pickerOptions: {
                shortcuts: [
                    {
                        text: "",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(
                                start.getTime() - 3600 * 1000 * 24 * 7
                            );
                            picker.$emit("pick", [start, end]);
                        }
                    },
                    {
                        text: "",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(
                                start.getTime() - 3600 * 1000 * 24 * 30
                            );
                            picker.$emit("pick", [start, end]);
                        }
                    },
                    {
                        text: "",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(
                                start.getTime() - 3600 * 1000 * 24 * 90
                            );
                            picker.$emit("pick", [start, end]);
                        }
                    }
                ]
            },
            value1: [
                new Date(2000, 10, 10, 10, 10),
                new Date(2000, 10, 11, 10, 10)
            ],
            value: []
        };
    },
    components: {
        PageViewList
    },
    mounted() {
        this.getBanTime();
        this.getUsers();
    },
    methods: {
        async getUsers() {
            let res = await this.$api.user.queryUserByGo();
            this.user_list = res.data;
        },
        async saveBanWhiteUsers() {
            let res = await this.$api.user.saveBanWhiteUser(this.banWhiteUser);
            this.$message("")
        },
        async getBanTime() {
            let res = await this.$api.user.getSysBanTime();
            this.banTime = res.data;
            this.banWhiteUser = this.banTime.user
        },
        async banOperate() {
            let res = await this.$api.user.setSysBanTime(
                this.time[0],
                this.time[1]
            );
            if (res.errcode == 0) {
                this.$message({
                    showClose: true,
                    message: "",
                    type: "success"
                });
                this.getBanTime();
            } else {
                this.$message({
                    showClose: true,
                    message: "",
                    type: "error"
                });
            }
        },
        async openOperate() {
            let res = await this.$api.user.cancelSysBan();
            if (res.errcode == 0) {
                this.$message({
                    showClose: true,
                    message: "",
                    type: "success"
                });
                this.getBanTime();
            } else {
                this.$message({
                    showClose: true,
                    message: "",
                    type: "error"
                });
            }
        },
        switchID: function() {
            if (this.user == "none") {
                this.$message({
                    showClose: true,
                    message: "",
                    type: "info"
                });
                return;
            }
            this.$confirm(
                "",
                "",
                {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                }
            )
                .then(() => {
                    this.doSwitchId(this.user);
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
        },
        async doSwitchId(user) {
            let res = await this.$api.user.switchUser(user);
            if (!res) {
                alert(111);
            }
            if (res.errcode == 0) {
                window.location.href = "/experiment";
            } else {
                this.$message({
                    type: "info",
                    message: res.errmsg
                });
            }
        }
    }
};
</script>

