<template>
    <el-col>
        <el-form :label-position="'top'" label-width="150px" v-model="layerInfo">
            <el-form-item label="">
                <el-input v-model="layerInfo.exp_layer_name" placeholder="20" @change="commitLayerInfo"></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-input
                    type="textarea"
                    v-model="layerInfo.exp_layer_comment"
                    rows="6"
                    placeholder=""
                    @change="commitLayerInfo"
                ></el-input>
            </el-form-item>

            <el-form-item label="A/B">
                <el-radio v-model="layerInfo.alloc_type_code" :label="2" @change="commitLayerInfo"></el-radio>
                <el-radio v-model="layerInfo.alloc_type_code" :label="3" @change="commitLayerInfo"></el-radio>
                <el-radio v-model="layerInfo.alloc_type_code" :label="1" @change="commitLayerInfo"></el-radio>
            </el-form-item>

            <el-form-item label="">
                <el-switch v-model="layerInfo.is_divide_budget" active-text="" @change="changeBudgetExp"></el-switch>
            </el-form-item>

            <el-form-item label="">
                <el-radio v-model="layerInfo.exp_object_type" label="CUSTID" @change="commitLayerInfo" :disabled="layerInfo.is_divide_budget"></el-radio>
                <el-radio v-model="layerInfo.exp_object_type" label="ADID" @change="commitLayerInfo" ></el-radio>
                <el-radio v-model="layerInfo.exp_object_type" label="MID" @change="commitLayerInfo" :disabled="layerInfo.is_divide_budget"></el-radio>
                <el-radio v-model="layerInfo.exp_object_type" label="TAG" @change="commitLayerInfo" :disabled="layerInfo.is_divide_budget"></el-radio>
            </el-form-item>

            <el-form-item label="">
                <el-input
                    type="textarea"
                    v-model="layerInfo.exp_object"
                    rows="6"
                    placeholder=""
                    @change="commitLayerInfo"
                ></el-input>
            </el-form-item>
        </el-form>
    </el-col>
</template>

<script>

export default {
    data() {
        return {
            layerInfo: {
                exp_layer_type : 1,
                alloc_type_code: 2,
                exp_layer_comment:
                    "\n\n\n\n",
                exp_layer_name: "",
                flow_type: 0,
                is_divide_budget : false,
                exp_object_type : "ADID",
                exp_object : "",
            },
            budgetExp: false
        };
    },
    mounted() {
        if (this.$store.state.create_exp.strategy_set_id < 1) {
            this.$emit("preStep");
            return;
        }
        if (
            Object.keys(this.$store.state.create_exp.exp_base_info).length > 0
        ) {
            console.log(this.$store.state.create_exp.exp_base_info);
            this.layerInfo = this.$store.state.create_exp.exp_base_info;
        }
    },
    methods: {
        changeBudgetExp() {
            if(this.layerInfo.is_divide_budget && this.layerInfo.exp_object_type != "ADID") {
                this.layerInfo.exp_object_type = "ADID"
                this.layerInfo.exp_object = "";
            }
            this.commitLayerInfo()
        },
        commitLayerInfo() {
            this.$store.commit("setExpBaseInfo", this.layerInfo);
            return;
        },
        checkInputData() {
            if (this.layerInfo.exp_layer_name == "") {
                return [""];
            }

            if (this.layerInfo.exp_layer_name.length > 20) {
                return [" 1  20"];
            }

            if (this.layerInfo.exp_layer_comment == "") {
                return [""];
            }

            if (this.layerInfo.exp_object == "") {
                return [""];
            }
            return [];
        }
    }
};
</script>
