<template>
    <div>
        <el-table :data="newListData" height="500" style="width: 100%">
            <el-table-column label="ID()" prop="quota.quota_id" width="140">
                <template slot-scope="scope">
                    <a
                        href="javascript:;"
                        @click="showQuotaDetail(scope.row)"
                    >{{scope.row.quota.quota_id}}</a>
                </template>
            </el-table-column>
            <el-table-column label="" prop="quota.quota_text"></el-table-column>
            <el-table-column label="" prop="quota.quota_en_text"></el-table-column>
            <el-table-column label="" width="300">
                <template slot-scope="scope">
                    <span>{{scope.row.support_product_list.join('')}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="quota.third_party_id" label="HubbleID"></el-table-column>
            <el-table-column prop="quota.quota_weight" label=""></el-table-column>
            <el-table-column prop="address" label="">
                <template slot-scope="scope">
                    <a href="javascript:;" @click="editQuota(scope.row)"></a>
                    <a href="javascript:;" @click="delQuota(scope.row)"></a>
                    <a href="javascript:;" @click="linkHubbleQuota(scope.row)"></a>
                </template>
            </el-table-column>
        </el-table>

        <el-drawer title="" :visible.sync="quotaDetail" direction="rtl">
            <span>
                <ul v-if="Object.keys(clickQuota).length>0">
                    <li class="drawer-item">
                        <b>ID</b>
                        {{clickQuota.quota.quota_id}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b>:</b>
                        {{clickQuota.quota.quota_text}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b></b>
                        {{clickQuota.quota.quota_en_text}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b>key</b>
                        {{clickQuota.quota.quota_name}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b></b>
                        {{clickQuota.quota_type_text}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b></b>
                        {{clickQuota.quota.quota_comment}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b></b>
                        {{clickQuota.quota_range_text}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b></b>
                        {{clickQuota.expression_text}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b></b>
                        {{clickQuota.quota.create_time}}
                    </li>
                    <el-divider></el-divider>
                </ul>
            </span>
        </el-drawer>

        <el-drawer title="" :visible.sync="editDrawer" direction="rtl" size="40%">
            <div style="padding:20px">
                <template v-if="Object.keys(clickQuota).length>0">
                    <el-form ref="form">
                        <el-form-item label=":">
                            <el-input v-model="clickQuota.quota.quota_text"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="clickQuota.quota.quota_en_text"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="clickQuota.quota.quota_comment" type="textarea"></el-input>
                        </el-form-item>
                        <el-form-item label="(0-100):">
                            <el-input v-model="clickQuota.quota.quota_weight"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select
                                v-model="product"
                                multiple
                                placeholder=""
                                style="width:400px"
                            >
                                <el-option label="" value="783" name="type"></el-option>
                                <el-option label="" value="1000" name="type"></el-option>
                                <el-option label="" value="11" name="type"></el-option>
                                <el-option label="" value="102" name="type"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-form>
                    <el-button type="danger" style="width:140px;" @click="updateQuota"></el-button>
                </template>
            </div>
        </el-drawer>

        <el-drawer title="hubble" :visible.sync="linkDrawer" direction="rtl" size="50%">
            <el-form style="padding:0 20px">
                <el-form-item label="Hubble:">
                    <el-input v-model="keyword" placeholder="idtheme"></el-input>
                </el-form-item>
            </el-form>
            <el-row style="height:400px;padding:0 20px;overflow:scroll;">
                <template v-if="Object.keys(clickQuota).length>0">
                    <el-form>
                        <el-form-item
                            style="height: 500px;overflow: scroll;display:flex;flex-direction:column;"
                        >
                            <el-radio-group v-model="hubbleId">
                                <el-radio label="0"></el-radio>
                                <template v-for="item in showHubbleQuotas">
                                    <el-radio :label="item.id">
                                        {{item.indicator_name}}
                                        <span
                                            v-if="hubbleId==item.id"
                                        >{{item.indicator_meaning}}</span>
                                    </el-radio>
                                </template>
                            </el-radio-group>
                        </el-form-item>
                    </el-form>
                </template>
            </el-row>
            <el-row style="padding:0 20px;">
                <el-button type="danger" @click="confirmLinkHubbleQuota"></el-button>
            </el-row>
        </el-drawer>
    </div>
</template>

<script>
export default {
    props: [
        "listData",
        "hubbleQuotas",
        "theModalId",
        "EditId",
        "thehubbleId",
        "linkId"
    ],
    data() {
        return {
            clickQuota: {},
            clickThirdQuota: {},
            keyword: "",
            hubbleId: 0,
            product: [],
            newListData: [],
            quotaDetail: false,
            editDrawer: false,
            hubbleDrawer: false,
            linkDrawer: false
        };
    },
    watch: {
        listData: {
            handler(newval, old) {
                this.newListData = newval;
            },
            immediate: true,
            deep: true
        }
    },
    computed: {
        showHubbleQuotas: function() {
            var result = this.hubbleQuotas.filter(function(data) {
                var reg = eval("/(?=" + this.keyword + ")/");

                let retData =
                    reg.test(data.id) ||
                    reg.test(data.indicator_name) ||
                    reg.test(data.indicator_meaning) ||
                    reg.test(data.theme);

                return retData;
            }, this);

            return result;
        }
    },
    methods: {
        showQuotaDetail: function(quota) {
            this.clickQuota = quota;
            this.quotaDetail = true;
        },
        showThirdPartyDetail(thirdData) {
            this.clickThirdQuota = thirdData;
            this.hubbleDrawer = true;
        },
        editQuota: function(quota) {
            this.product = quota.quota.product.split(",");
            this.clickQuota = quota;
            this.editDrawer = true;
        },
        linkHubbleQuota: function(quota) {
            this.clickQuota = quota;
            this.linkDrawer = true;
            this.hubbleId = this.clickQuota.third_party_id;
        },
        confirmLinkHubbleQuota() {
            this.updateQuotaThirdId(
                this.clickQuota.quota.quota_id,
                this.hubbleId
            );
        },
        async updateQuotaThirdId(quotaId, thirdId) {
            let res = await this.$api.quota.updateByGo({
                quota_id: parseInt(quotaId),
                third_party_id: parseInt(thirdId)
            });

            if (res.errcode != 0) {
                this.$message.error(res.errmsg || "");
                return;
            }
            this.$message({
                message: "",
                type: "success"
            });

            this.$emit("updateData");
        },
        async delQuota(data) {
            this.$confirm("", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning"
            })
                .then(async () => {
                    let res = await this.$api.quota.deleteByGo(
                        data.quota.quota_id
                    );
                    if (res.errcode != 0) {
                        this.$message.error(res.errmsg || "");
                        return;
                    }
                    this.$message({
                        type: "success",
                        message: "!"
                    });
                    this.$emit("updateData");
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
        },
        async updateQuota() {
            let quotaParam = this.clickQuota.quota;
            quotaParam.support_product = this.clickQuota.support_product;
            let postData = this.$lodash.pick(quotaParam, [
                "quota_id",
                "quota_text",
                "quota_en_text",
                "quota_comment",
                "quota_weight"
            ]);
            postData.quota_id = parseInt(postData.quota_id);
            postData.quota_weight = parseInt(postData.quota_weight);
            postData.product = this.product.join(",");
            console.log(postData);
            //return
            let res = await this.$api.quota.updateByGo(postData);

            if (res.errcode != 0) {
                this.$message.error(res.errmsg || "");
                return;
            }
            this.$message({
                message: "",
                type: "success"
            });
            this.$emit("updateData");
        }
    }
};
</script>

<style lang="less" scoped>
.drawer-item {
    list-style-type: none;
}
.el-radio-group {
    display: flex;
    flex-direction: column;
}
</style>
