<template>
    <el-col>
        <el-form :label-position="'top'" label-width="150px" :model="layerInfo">
            <el-form-item label="" v-if="showDomainInput">
                <el-slider v-model="layerInfo.traffic_weight" :min="0" :max="restTrafficWeight" :step="10" @change="commitLayerInfo"></el-slider>
                <p><el-tag>{{layerInfo.traffic_weight}}</el-tag><el-tag type="warning">{{1000 - restTrafficWeight}}</el-tag>
                <el-tag type="success">{{restTrafficWeight - layerInfo.traffic_weight}}</el-tag> </p>
            </el-form-item>

            <el-form-item label="">
                <el-input
                    v-model="layerInfo.exp_layer_name"
                    placeholder="20"
                    @change="commitLayerInfo"
                ></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-input
                    type="textarea"
                    v-model="layerInfo.exp_layer_comment"
                    @change="commitLayerInfo"
                    rows="6"
                    placeholder=""
                ></el-input>
            </el-form-item>

            <el-form-item label="A/B">
                <el-radio
                    v-model="layerInfo.alloc_type_code"
                    :label="2"
                    @change="commitLayerInfo"
                ></el-radio>
                <el-radio
                    v-model="layerInfo.alloc_type_code"
                    :label="3"
                    @change="commitLayerInfo"
                ></el-radio>
                <el-radio
                    v-model="layerInfo.alloc_type_code"
                    :label="1"
                    @change="commitLayerInfo"
                ></el-radio>
            </el-form-item>

            <el-form-item label="">
                <el-radio
                    v-model="layerInfo.alloc_condition_enable"
                    :label="0"
                    @change="commitLayerInfo"
                ></el-radio>
                <el-radio
                    v-model="layerInfo.alloc_condition_enable"
                    :label="1"
                    @change="commitLayerInfo"
                ></el-radio>
            </el-form-item>
        </el-form>

        <el-card class="box-card" v-if="layerInfo.alloc_condition_enable == 1">
            <FlowAlloc v-show="layerInfo.alloc_condition_enable == '1'"></FlowAlloc>
        </el-card>
    </el-col>
</template>

<script>
import FlowAlloc from "./FlowAlloc.vue";
import lodash from "lodash";
import moment from "moment";
import { mapGetters } from "vuex";

export default {
    data() {
        return {
            layerInfo: {
                alloc_type_code: 2,
                exp_layer_comment:
                    "\n\n\n\n",
                exp_layer_name: "",
                alloc_condition_enable: 0,
                flow_type: 1,
                traffic_weight : 0,
            },
            restTrafficWeight : 1000,
        };
    },
    components: {
        FlowAlloc
    },
    computed: {
        ...mapGetters({
            showDomainInput: "show_domain"
        })
    },
    mounted() {
        if (this.$store.state.create_exp.strategy_set_id < 1) {
            this.$emit("preStep");
            return;
        }
        if (
            Object.keys(this.$store.state.create_exp.exp_base_info).length > 0
        ) {
            console.log(this.$store.state.create_exp.exp_base_info)
            this.layerInfo = this.$store.state.create_exp.exp_base_info;
        }
        this.getRestDomain()
    },
    methods: {
        commitLayerInfo() {
            this.$store.commit("setExpBaseInfo", this.layerInfo);
            return;
        },
        checkInputData() {
            if (this.layerInfo.exp_layer_name == "") {
                return [""];
            }

            if (this.layerInfo.exp_layer_name.length > 60) {
                return [" 1  60"];
            }

            if (this.layerInfo.exp_layer_comment == "") {
                return [""];
            }

            if (this.showDomainInput && this.layerInfo.traffic_weight < 1) {
                return [""];
            }
            return [];
        },
        async getRestDomain() {
            if (!this.showDomainInput) {
                return;
            }
            let res = await this.$api.strategy.getStrategyExpStat(
                this.$store.state.create_exp.strategy_set_id,
            );
            this.restTrafficWeight = 1000 - res.data.use_traffic;
        }
    }
};
</script>
